<template>
    <div class="task-execution-step-wraper" :class="{ column: taskExecutionDetail.taskExecution.isTask }">
        <div v-if="taskExecutionDetail.taskExecution.isTask" class="task-process">
            <div class="process-wraper">
                <scroll-faker>
                    <task-step-start />
                    <task-step
                        v-for="step in taskExecutionDetail.stepExecution"
                        :data="step"
                        :active-id="currentStepInstanceId"
                        @on-select="handleChangeStep"
                        @on-update="handleTaskStatusUpdate"
                        :key="step.stepInstanceId" />
                    <task-step-end :disable="!taskExecutionDetail.taskExecution.isSuccess" />
                </scroll-faker>
            </div>
            <div class="execution-process">
                {{ currentStepOrder }} / {{ taskExecutionDetail.totalStep }}
            </div>
        </div>
        <div class="task-step-detail">
            <slot />
        </div>
        <div v-if="historyEnable" class="execution-history" :class="{ active: isShowHistory }">
            <div class="toggle-btn" @click="handleShowHistory">
                <Icon class="toggle-flag" type="angle-double-left" />
                <div class="return-edit">{{ '返回编辑' }}</div>
            </div>
            <div class="history-content">
                <template v-if="taskExecutionDetail.taskExecution.isScript">
                    <div
                        v-for="item in scriptHistoryList"
                        class="item"
                        :key="item.id"
                        @click="handleGoRedoScriptExec(item)">
                        {{ item.name }}
                    </div>
                </template>
                <template v-if="taskExecutionDetail.taskExecution.isFile">
                    <div
                        v-for="item in fileHistoryList"
                        class="item"
                        :key="item.id"
                        @click="handleGoRedoFileExec(item)">
                        {{ item.name }}
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
    import _ from 'lodash';
       import TaskExecuteService from '@service/task-execute';
    import {
        buildURLParams,
    } from '@utils/assist';
    import {
        execScriptHistory,
        pushFileHistory,
    } from '@utils/cache-helper';
    import TaskStep from './task-step/index';
    import TaskStepStart from './task-step/start';
    import TaskStepEnd from './task-step/end';
    import mixins from './mixins';

    export default {
        name: '',
        components: {
            TaskStep,
            TaskStepStart,
            TaskStepEnd,
        },
        mixins: [
            mixins,
        ],
        data () {
            return {
                isLoading: false,
                isShow: false,
                taskExecutionDetail: {
                    isFinished: false,
                    taskExecution: {},
                    stepExecution: [],
                },
                scriptHistoryList: [{},{},{}],
                fileHistoryList: [{},{},{}],
                isShowHistory: false,
                isFinished: false,
                currentStepInstanceId: '',
            };
        },
        computed: {
            currentStepOrder () {
                return _.findIndex(
                    this.taskExecutionDetail.stepExecution,
                    _ => _.stepInstanceId === this.currentStepInstanceId,
                ) + 1;
            },
            historyEnable () {
                if (this.taskExecutionDetail.taskExecution.isScript) {
                    return this.scriptHistoryList.length > 0;
                }
                if (this.taskExecutionDetail.taskExecution.isFile) {
                    return this.fileHistoryList.length > 0;
                }
                return false;
            },
        },
        created () {
            this.timer = '';

            const { taskInstanceId } = this.$route.params;
            const { stepInstanceId, retryCount = 0 } = this.$route.query;
            
            this.taskInstanceId = parseInt(taskInstanceId, 10);
            this.stepInstanceId = parseInt(stepInstanceId, 10);
            this.retryCount = parseInt(retryCount, 10);
            
            this.fetchData();
        },
        mounted () {
            this.initHistory();
            this.$once('hook:beforeDestroy', () => {
                clearTimeout(this.timer);
            });
        },
        methods: {
            fetchData (trigger = false) {
                this.isLoading = true;
                TaskExecuteService.fetchTaskExecutionResult({
                    id: this.taskInstanceId,
                }).then((data) => {
                    this.taskExecutionDetail = Object.freeze(data);
                    if (!data.finished) {
                        this.timer = setTimeout(() => {
                            this.reLoading();
                        }, 1000);
                    }
                    // 没有指定stepInstanceId默认取第一个步骤
                    if (!this.stepInstanceId) {
                        const [
                            {
                                stepInstanceId,
                                retryCount,
                            },
                        ] = data.stepExecution;
                        this.stepInstanceId = stepInstanceId;
                        this.retryCount = retryCount;
                    }
                    this.currentStepInstanceId = this.stepInstanceId;
                    this.$emit('on-init', {
                        taskInstanceId: this.taskInstanceId,
                        stepInstanceId: this.stepInstanceId,
                        retryCount: this.retryCount,
                        taskStepList: data.stepExecution,
                        isTask: this.taskExecutionDetail.taskExecution.isTask,
                    });
                })
                    .catch((error) => {
                        if ([
                            400,
                            1244006,
                        ].includes(error.code)) {
                            setTimeout(() => {
                                this.$router.push({
                                    name: 'historyList',
                                });
                            }, 3000);
                        }
                    })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            // 作业执行状态轮询
            reLoading () {
                // 是作业类型的任务才会轮询作业的状态
                if (!this.taskExecutionDetail.taskExecution.isTask) {
                    return;
                }
                TaskExecuteService.fetchTaskExecutionResult({
                    id: this.taskInstanceId,
                }).then((data) => {
                    this.taskExecutionDetail = Object.freeze(data);
                    if (!data.finished) {
                        this.$pollingQueueRun(this.reLoading);
                    }
                });
            },
            initHistory () {
                this.scriptHistoryList = Object.freeze(execScriptHistory.getItem());
                this.fileHistoryList = Object.freeze(pushFileHistory.getItem());
            },
            handleShowHistory () {
                this.isShowHistory = !this.isShowHistory;
            },
            handleGoRedoScriptExec (payload) {
                this.$router.push({
                    name: 'fastExecuteScript',
                    params: {
                        taskInstanceId: payload.taskInstanceId,
                    },
                    query: {
                        from: 'historyStep',
                    },
                });
            },
            handleGoRedoFileExec (payload) {
                this.$router.push({
                    name: 'fastPushFile',
                    params: {
                        taskInstanceId: payload.taskInstanceId,
                    },
                    query: {
                        from: 'historyStep',
                    },
                });
            },
            handleChangeStep (step) {
                if (step.stepInstanceId === this.currentStepInstanceId) {
                    return;
                }

                if (step.isNotStart) {
                    this.$bkMessage({
                        theme: 'warning',
                        message: '该步骤还未执行',
                        limit: 1,
                    });
                    return;
                }

                if (step.isApproval) {
                    this.$bkMessage({
                        theme: 'warning',
                        message: '人工确认步骤不支持查看步骤详情',
                        limit: 1,
                    });
                    return;
                }
                
                const { stepInstanceId, retryCount } = step;
                this.currentStepInstanceId = stepInstanceId;
                const query = buildURLParams({
                    ...this.$route.query,
                    stepInstanceId,
                    retryCount,
                });
                window.history.replaceState({}, '', `?${query}`);
                
                this.$emit('on-init', {
                    stepInstanceId,
                    taskInstanceId: this.taskInstanceId,
                    retryCount,
                    taskStepList: this.taskExecutionDetail.stepExecution,
                    isTask: this.taskExecutionDetail.taskExecution.isTask,
                });
            },
            handleTaskStatusUpdate (payload) {
                this.reLoading();
            },
        },
    };
</script>
<style lang='postcss'>
    html[lang="en-US"] {
        .return-edit {
            margin-top: 6px;
            transform: rotate(90deg);
        }
    }

    @keyframes ani-rotate {
        to {
            transform: rotateZ(360deg);
        }
    }

    .task-execution-step-wraper {
        background: #fff;

        &.column {
            display: flex;

            .task-step-detail {
                width: 0;
            }
        }

        .task-process {
            display: flex;
            height: calc(100vh - 104px);
            padding-bottom: 38px;
            background: #fff;
            border-right: 1px solid #e2e2e2;
            flex-direction: column;
            flex: 0 0 61px;

            .process-wraper {
                height: 100%;

                .scroll-faker-content {
                    position: relative;
                }
            }

            .execution-process {
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                width: 60px;
                height: 28px;
                font-size: 14px;
                line-height: 28px;
                color: #fff;
                text-align: center;
                background: rgb(0 0 0 / 25%);
                user-select: none;
            }
        }

        .task-step-detail {
            flex: 1;
            background: #fff;
        }

        .execution-history {
            position: fixed;
            top: 127px;
            right: 0;
            z-index: 999;
            font-size: 12px;
            line-height: 30px;
            color: #c4c6cc;
            background: #63656e;
            border-bottom-left-radius: 2px;
            transform: translateX(100%);
            transition: all 0.35s;
            user-select: none;

            &.active {
                transform: translateX(0);

                .toggle-flag {
                    transform: rotateZ(180deg);
                }
            }

            .toggle-btn {
                position: absolute;
                top: 0;
                left: -22px;
                display: flex;
                width: 22px;
                height: 88px;
                line-height: 13px;
                color: #dcdee5;
                text-align: center;
                cursor: pointer;
                background: #63656e;
                border-right: 1px solid #757783;
                border-bottom-left-radius: 8px;
                border-top-left-radius: 8px;
                flex-direction: column;
                justify-content: center;

                .toggle-flag {
                    margin-bottom: 5px;
                    color: #979ba5;
                    transition: all 0.2s;
                }
            }

            .history-content {
                display: flex;
                min-height: 90px;
                padding: 12px 0;
                flex-direction: column;
                justify-content: center;
            }

            .item {
                padding-right: 16px;
                padding-left: 16px;
                cursor: pointer;
                transition: all 0.15s;

                &:hover {
                    color: #fff;
                    background: #4f515a;
                }
            }
        }
    }
</style>
